  background-attachment: fixed;
  background-size: cover;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.card {
  background-color: var(--clr_euro02);
  box-shadow: 10px 12px 37px 2px rgba(14, 14, 14, 0.25);
  -webkit-box-shadow: 10px 12px 37px 2px rgba(14, 14, 14, 0.25);
  -moz-box-shadow: 10px 12px 37px 2px rgba(14, 14, 14, 0.25);
  color: #282c34;
  border-radius: 20px;
  width: 80%;
  height: 600px;
  display: flex;
  user-select: none;
}

.textoCard {
  display: flex;
  flex-direction: column;
  align-items: center;
  top: 0;
  padding: 15px 30px;
  transform: translateY(-250px);
  transition: 0.5s ease-in-out;
}

.textoCard h1 {
  text-shadow: 0px 3px 7px black;
  margin-bottom: 20px;
}

.textoCard h2 {
  margin-top: 0px;
  font-size: 1em;
  text-shadow: 0px 3px 7px black;
}

.cardImage {
  background-color: var(--clr_euro03);
  border-radius: 20px;
  width: 90%;
  box-shadow: 5px 0px 18px 2px rgb(14 14 14 / 28%);
  color: white;
  cursor: pointer;
}

.cardInfo {
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 20px;
  width: 50%;
  opacity: 0%;
  transform: translateY(-150px);
  transition: 0.5s ease-in-out;
}

.cardImageArq {
  object-fit: cover;
}

.cardImage:hover .textoCard {
  transform: translateY(-475px);
}

.cardImage:hover .cardInfo {
  opacity: 75%;
  transform: translateY(0px);
}

img {
  width: 100%;
  height: 100%;
  border-radius: 20px;
}

.cardButton {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 10%;
  height: 600px;
  justify-content: center;
  row-gap: 25px;
  color: var(--clr_euro03);
  font-size: 15px;
}

