@media screen and (max-width: 1500px) {
  .cardImage {
    width: 85%;
  }

  .cardButton {
    width: 15%;
  }
}

@media screen and (max-width: 1000px) {
  .cardInfo {
    width: 75%;
  }
}

@media screen and (max-width: 900px) {
  .card {
    flex-direction: column;
  }

  .cardImage {
    width: 100%;
    height: 400px;
  }

  .cardButton {
    width: 100%;
    height: 150px;
    row-gap: 10px;
    margin-top: 20px;
  }

  .cardButtonExterior {
    flex-direction: row;
    column-gap: 6px;
    padding-bottom: 5px;
    padding-top: 5px;
  }

  .cardButtonInterior {
    flex-direction: row;
    column-gap: 6px;
    padding-bottom: 5px;
    padding-top: 5px;
  }

  .textoCard {
    transform: translateY(-220px);
    height: 150px;
  }

  .cardInfo {
    transform: translateY(-330px);
  }

  .cardImage:hover .textoCard {
    transform: translateY(-200px);
  }

  .cardImage:hover .cardInfo {
    opacity: 75%;
    transform: translateY(-325px);
  }
}
